
  .app {
    margin: 1rem;
  }

  input {
    padding: 0.5rem;
    font-size: 16px;
    width: 100%;
    display: block;
    border-radius: 4px;
    border: 1px solid #ccc;
  }

  input:focus {
    border-color: #007eff;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
      0 0 0 3px rgba(0, 126, 255, 0.1);
    outline: none;
  }

  input.error {
    border-color: red;
  }

  label {
    font-weight: bold;
    display: block;
    margin-bottom: 0.5rem;
    margin-top: 0.5rem;
  }

  .input-feedback {
    color: red;
    margin-top: 0.25rem;
  }
